.header {
  display: grid;
  grid-template-columns: 1fr min-content;
  grid-template-rows: min-content min-content;
  margin: 0 -15px;
}

.header__bg {
  padding-right: -15px;
  padding-left: -15px;
  background-color: rgba(245, 245, 250, 0.2);
}

.header__line {
  grid-column: span 2;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: -15px;
  margin-right: -15px;
  background-color: rgba(245, 245, 250, 0.2);
}

.header__address {
  display: none;
  font-size: 16px;
  line-height: 20px;
  text-align: right;
  color: #B3B3BA;
  text-transform: none;
  text-decoration: none;
  position: relative;
  margin-left: 18px;
}

.header__address::before {
  content: '';
  background-image: url(../img/header-dot.svg);
  background-repeat: no-repeat;
  position: absolute;
  left: -12px;
  top: 7px;
  width: 6px;
  height: 6px;
}

.header__address::after {
  content: '';
  background-image: url(../img/header-dot.svg);
  background-repeat: no-repeat;
  position: absolute;
  right: -11px;
  top: 7px;
  width: 6px;
  height: 6px;
}

.header__address--home {
  color: #FFFFFF;
}

.header__fb {
  display: none;
  position: relative;
  margin-left: 18px;
}

.header__fb::before {
  content: '';
  background-image: url(../img/header-dot.svg);
  background-repeat: no-repeat;
  position: absolute;
  left: -12px;
  top: 10px;
  width: 6px;
  height: 6px;
}

.header__icon:hover path {
  fill: #F0145A;
}

.header__phone {
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  text-align: right;
  color: #B3B3BA;
  text-transform: none;
  text-decoration: none;
  position: relative;
  margin-left: 18px;
}

.header__phone--home {
  color: #FFFFFF;
}

.header__logobox {
  display: flex;
  align-items: flex-start;
  padding-top: 15px;
  padding-bottom: 10px;
}

.header__logo img {
  width: 69px;
  height: auto;
}

.header__text {
  font-size: 20px;
  line-height: 24px;
  color: #323264;
  text-transform: none;
  text-decoration: none;
  padding-left: 20px;
  position: relative;
}

.header__text::before {
  content: '';
  background-image: url(../img/slash.svg);
  background-repeat: no-repeat;
  background-size: 12px 26px;
  position: absolute;
  left: 4px;
  top: 0;
  width: 12px;
  height: 26px;
}

.header__text::before img {
  display: block;
}

.header__text--home {
  color: #FFFFFF;
}

.header__lang {
  display: flex;
  align-items: center;
}

.header__lang a {
  font-weight: 700;
  font-size: 12px;
  line-height: 15px;
  text-align: center;
  letter-spacing: 1px;
  color: #B3B3BA;
  padding: 5px 8px;
  border-radius: 8px;
  text-decoration: none;
  text-transform: uppercase;
}

.header__lang--home a {
  color: #FFFFFF;
}

.header__lang a:hover {
  background: #F5F5FA;
}

.header__lang--home a:hover {
  background: rgba(255, 255, 255, 0.2);
}

.header__lang a.current {
  color: #FFFFFF;
  background: #F0145A;
}

.header__btn {
  cursor: pointer;
  border: none;
  background-color: transparent;
  background-image: url(../img/hamburger.svg);
  width: 32px;
  height: 32px;
}

.header__btn--home {
  background-image: url(../img/hamburger-home.svg);
}

.header__menu {
  padding-top: 13px;
}

.header__item {
  display: none;
  background-color: #FFFFFF;
  border-radius: 12px 12px 0px 0px;
  padding: 12px 12px 0 12px;
  width: 360px;
  position: absolute;
  right: 0;
  top: 46px;
}

.header__switch {
  display: flex;
  justify-content: flex-end;
}

.header__button {
  height: 32px;
  width: 32px;
  border: none;
  background-color: transparent;
  background-image: url(../img/header-cross.svg);
  margin-bottom: 20px;
}

.header__list {
  list-style: none;
  padding: 0;
}

.header__list li {
  margin-bottom: 15px;
}

.header__list a {
  font-weight: 700;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  color: #24A3FF;
  text-decoration: none;
  text-transform: none;
  padding: 7px;
  background: #F5F5FA;
  border-radius: 12px;
  display: block;
}

.header__list a:active {
  background: #E6E6F0;
}

.header__list a.current {
  color: #F0145A;
}

@media (min-width: 768px) {
  .header {
    margin: 0;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(3, min-content);
  }
  .header__line {
    grid-column: 1 / -1;
    justify-content: flex-start;
    padding-top: 50px;
    padding-bottom: 0;
    padding-left: 0;
    padding-right: 0;
    margin-left: 0;
    margin-right: 0;
    background-color: transparent;
  }
  .header__address {
    display: block;
    margin-left: 16px;
  }
  .header__phone {
    margin-left: 22px;
  }
  .header__fb {
    display: block;
  }
  .header__logobox {
    padding-top: 28px;
    padding-bottom: 13px;
  }
  .header__logo img {
    width: 130px;
  }
  .header__text {
    padding-left: 29px;
    font-size: 36px;
    line-height: 44px;
  }
  .header__text::before {
    background-image: url(../img/slash.svg);
    background-size: 21px 47px;
    width: 21px;
    height: 47px;
  }
  .header__btn {
    display: none;
  }
  .header__item {
    display: block;
    position: static;
    width: auto;
    padding: 0;
    border-radius: 0;
    background-color: transparent;
  }
  .header__switch {
    display: none;
  }
  .header__list {
    display: flex;
  }
  .header__list li {
    margin-bottom: 0;
    margin-right: 16px;
  }
  .header__list li:last-child {
    margin-right: 0;
  }
  .header__list a {
    padding: 0;
    background: transparent;
    border-radius: 0;
    font-size: 16px;
    line-height: 20px;
    padding-bottom: 3px;
    border-bottom: 2px solid transparent;
  }
  .header__list--home a {
    color: #FFFFFF;
  }
  .header__list a:hover {
    border-color: #24A3FF;
  }
  .header__list--home a:hover {
    border-color: #FFFFFF;
  }
  .header__list a.current {
    color: #F0145A;
    border-color: #F0145A;
  }
}

@media (min-width: 992px) {
  .header {
    grid-template-columns: auto 1fr;
    grid-template-rows: min-content min-content;
  }
  .header__logobox {
    grid-row: 1 / -1;
    padding-top: 51px;
    padding-bottom: 0;
  }
  .header__line {
    grid-row: 1 / 2;
    grid-column: 2 / -1;
    justify-content: flex-end;
    padding-top: 50px;
    padding-bottom: 0;
  }
  .header__menu {
    justify-self: end;
    padding-top: 14px;
  }
  .header__phone {
    margin-left: 18px;
  }
}
